@use '@miq/styles/src/mixins' as *;

.p-update-route {
  @include lg-screen {
    position: absolute;
  }

  .col {
    @include lg-screen {
      height: 100vh;
      overflow-y: auto;
    }
  }
}

.product-staff-table {
  tbody th {
    width: 5rem;
    background-color: white;
  }

  tbody tr {
    &:hover {
      background-color: var(--blue-50, white);
      th {
        background-color: var(--blue-50, white);
      }
    }
  }
}
